import React from "react";
import { useAuth } from "../contexts/AuthContext";
import "../styles/InicioScreen.css";

export default function InicioScreen() {
    const { user, logout } = useAuth();
    
    return (
        <div className="inicio-container">
            <div className="inicio-card">
                <h1 className="inicio-title">
                     Bienvenido a Joyera Diana Laura
                </h1>
                
                <p className="user-email">
                    Hola, <strong>{user?.nombre}</strong> ({user?.email})
                </p>
                
                <button 
                    className="logout-button"
                    onClick={logout}
                >
                    Cerrar Sesin
                </button>
                
                <div className="inicio-info">
                    <h3> Sistema de Gestin</h3>
                    <p>
                        Bienvenido al sistema de gestin de inventario y ventas 
                        de Joyera Diana Laura. Aqu podrs administrar productos, 
                        clientes y ventas de forma eficiente.
                    </p>
                </div>
            </div>
        </div>
    );
}